import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';

@Component({
  template: `
    <ion-list>
      <button ion-item (click)="reportProblem(); close()">Report problem</button>
      <button ion-item (click)="toggleTaskView(); close()">
        View
        <span *ngIf="metadata.itemsProperty === \'incompleteItems\'">completed</span>
        items
      </button>
    </ion-list>
  `
})
export class InspectionPopover {
  reportProblem: any;
  toggleTaskView: any;
  metadata: any;

  constructor(public navCtrl: NavController, public viewCtrl: ViewController, private navParams: NavParams) {}

  ngOnInit() {
    if (this.navParams.data) {
      this.reportProblem = this.navParams.data.reportProblem;
      this.toggleTaskView = this.navParams.data.toggleTaskView;
      this.metadata = this.navParams.data.itemsProperty;
    }
  }

  close() {
    this.viewCtrl.dismiss();
  }
}



