import { NgModule, ErrorHandler } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { InspectionPage } from '../pages/inspection/inspection';
import { AdmininspectionPage } from '../pages/admininspection/admininspection';
import { InspectionPopover } from '../pages/inspection/popover';
import { ProblemPage } from '../pages/problem/problem';
import { Problem1Page } from '../pages/problem1/problem1';
import { KanbanPage } from '../pages/kanban/kanban';
import { Kanban1Page } from '../pages/kanban1/kanban1';
import { Data } from '../providers/data';
import { DragulaModule } from 'ng2-dragula';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    InspectionPage,
    InspectionPopover,
    ProblemPage,
    Problem1Page,
    Kanban1Page,
    KanbanPage
  ],
  imports: [
    IonicModule.forRoot(MyApp),
    DragulaModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    InspectionPage,
    InspectionPopover,
    ProblemPage,
    Problem1Page,
    Kanban1Page,
    KanbanPage
  ],
  providers: [Data, {provide: ErrorHandler, useClass: IonicErrorHandler}]
})
export class AppModule {}



