import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { InspectionPage } from '../inspection/inspection';
import { KanbanPage } from '../kanban/kanban';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  units: any;

  constructor(public navCtrl: NavController) {

    this.units = [
      {id: 'VI1204', name: 'Valley Isle 1204'},
      {id: 'KR414', name: 'Kanaha Ridge 414'},
      {id: 'KR121', name: 'Kanaha Ridge 121'}
    ];
  }

  editUnit(unit){
    console.log('edit: '+unit.id);
    this.navCtrl.push(InspectionPage, {
      unit: unit
    });
  }

  goToKanbanPage() {
      this.navCtrl.push(KanbanPage);
    }
}



